
body {
  background-color: black;
  font-family: courier;
}

h1, p {
  font-family: Lato;
}

ul {
  padding: 0;
}

li {
  display: flex;
}

li:nth-child(even) {
  background-color: #222;
}

li:nth-child(odd) {
  background-color: #444;
}

li {
  padding: 8px;
  color: rgb(255, 79, 79);
}

li.header {
  color: white
}

li.running {
  color: rgb(68, 179, 68);
}

.processName {
  width: 50%;
}

.processStatus {
  width: 20%;
}

.action {
  opacity: 0.6;
  color: white;
  margin: 0 8px;
  cursor: pointer;
}

.action:hover {
  opacity: 1.0;
}

.action.debug {
  color: #6eb96e
}

.action.play {
  color: #9cdcfe
}

.action.kill {
  color: #c9c89d
}

.action.restart {
  color: #df7721
}

.dnsRecord {
  color: white;
}
.recordType {
  width: 10%;
}

.domain {
  width: 40%;
}

.target {
  width: 40%;
}

.port {
  width: 10%;
}

.applyPrompt {
  width: 10%;
}

.applyInput {
  width: 90%;
}

.applyInput > input[type=text] {
  margin-left: -3px;
  font-size: 1em;
  font-family: Courier;
  outline: none;
  background-color: transparent;
  color: white;
  -webkit-appearance: caret;
  height: 18px;
  width: 100%;
}